import { Component, OnInit } from '@angular/core';
import {ConfigService} from './../config.service'
import { HttpClient } from '@angular/common/http';
import { FormsModule } from '@angular/forms';
import { Router,ActivatedRoute } from '@angular/router';
import { ToastController } from '@ionic/angular';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  public user;
  constructor(public router:Router,public toastController: ToastController,private host:ConfigService,private http: HttpClient,private form:FormsModule) { 
  }

  ngOnInit() {
    this.user = {name:"",pass:""}
  }
  login(user){
    this.http.post<any>(this.host.getHost() + '/authUser',user).subscribe(data => {
      console.log(data)
      if(data.length>0){
        this.host.setHost(data[0].ID)
        this.router.navigate(['start'], { queryParams: { userID: data[0].ID } });
        //window.location.replace("/start?userID="  + data[0].ID)
      }else{
        this.presentToast()
      }
    })
    
  }
  async presentToast() {
    const toast = await this.toastController.create({
      message: 'Error de usuario, intente de nuevo',
      duration: 2000
    });
    toast.present();
  }
}
